import React, { useState, useEffect } from 'react';
import {
  Box,
  VStack,
  HStack,
  Text,
  Button,
  Card,
  CardBody,
  Badge,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalCloseButton,
  ModalBody,
  Textarea,
  useDisclosure,
  useToast,
  Alert,
  AlertIcon
} from '@chakra-ui/react';
import { useAuth } from '../contexts/AuthContext';
import { SessionStorageManager, SessionStorageKeys } from '../utils/sessionStorage';

interface QnAItem {
  id: string;
  question: string;
  answer?: string;
  author: string;
  studentName: string;
  createdAt: string;
  answeredAt?: string;
  isPrivate: boolean;
}

const mockQnA: QnAItem[] = [
  {
    id: '1',
    question: '       ?',
    answer: ' , , ,       .',
    author: '',
    studentName: '',
    createdAt: '2024-03-15',
    answeredAt: '2024-03-16',
    isPrivate: true
  },
  {
    id: '2',
    question: '      .',
    author: '',
    studentName: '',
    createdAt: '2024-03-14',
    isPrivate: true
  }
];

export const QnAPage: React.FC = () => {
  const { user } = useAuth();
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [question, setQuestion] = useState('');
  const [qnaList, setQnaList] = useState<QnAItem[]>([]);
  const toast = useToast();

  useEffect(() => {
    loadQnAData();
  }, []);

  const loadQnAData = () => {
    const storedQnA = SessionStorageManager.get<QnAItem[]>(SessionStorageKeys.QNA, []);
    setQnaList(storedQnA);
  };

  const handleSubmitQuestion = () => {
    if (!question.trim()) {
      toast({
        title: ' ',
        status: 'warning',
        duration: 2000,
      });
      return;
    }

    const newQuestion: QnAItem = {
      id: Date.now().toString(),
      question: question.trim(),
      author: '',
      studentName: user?.profile?.name || '',
      createdAt: new Date().toISOString().split('T')[0],
      isPrivate: true
    };

    const updatedQnA = [newQuestion, ...qnaList];
    setQnaList(updatedQnA);
    SessionStorageManager.set(SessionStorageKeys.QNA, updatedQnA);
    setQuestion('');
    onClose();
    
    toast({
      title: ' ',
      description: '    .',
      status: 'success',
      duration: 3000,
    });
  };

  const handleAnswerQuestion = (id: string, answer: string) => {
    const updatedQnA = qnaList.map(item => 
      item.id === id ? { ...item, answer, answeredAt: new Date().toISOString().split('T')[0] } : item
    );
    setQnaList(updatedQnA);
    SessionStorageManager.set(SessionStorageKeys.QNA, updatedQnA);
  };

  if (!user) {
    return (
      <Box textAlign="center" py={20}>
        <Alert status="warning" maxW="md" mx="auto">
          <AlertIcon />
            .
        </Alert>
      </Box>
    );
  }

  return (
    <Box maxW="1200px" mx="auto" p={6} bg="gray.900" minH="100vh" color="white">
      <HStack justify="space-between" mb={8}>
        <Text fontSize="3xl" fontWeight="bold" color="#7A5AF8">
          
        </Text>
        {user.role === 'student' && (
          <Button 
            bg="#7A5AF8"
            color="white"
            _hover={{ bg: "#6B4CE6" }}
            onClick={onOpen}
          >
            
          </Button>
        )}
      </HStack>

      {user.role === 'student' && (
        <Alert status="info" mb={6} bg="blue.800" color="white">
          <AlertIcon />
             ,     .
        </Alert>
      )}

      <VStack spacing={4} align="stretch">
        {qnaList.map((item) => (
          <Card key={item.id} bg="gray.800" border="1px solid" borderColor="gray.600">
            <CardBody>
              <VStack align="stretch" spacing={4}>
                <Box>
                  <HStack justify="space-between" mb={2}>
                    <HStack>
                      <Badge colorScheme="purple"></Badge>
                      {user.role === 'admin' && (
                        <Badge colorScheme="blue">
                          : {item.studentName}
                        </Badge>
                      )}
                    </HStack>
                    <Text fontSize="sm" color="gray.400">
                      {item.createdAt}
                    </Text>
                  </HStack>
                  <Text color="white">{item.question}</Text>
                </Box>

                {item.answer ? (
                  <Box bg="gray.700" p={4} borderRadius="md">
                    <HStack justify="space-between" mb={2}>
                      <Badge colorScheme="green"></Badge>
                      <Text fontSize="sm" color="gray.300">
                         
                      </Text>
                    </HStack>
                    <Text color="white">{item.answer}</Text>
                  </Box>
                ) : (
                  user.role === 'admin' && (
                    <Box>
                      <AdminAnswerForm 
                        questionId={item.id}
                        onSubmit={(answer) => handleAnswerQuestion(item.id, answer)}
                      />
                    </Box>
                  )
                )}
              </VStack>
            </CardBody>
          </Card>
        ))}

        {qnaList.length === 0 && (
          <Card bg="gray.800" border="1px solid" borderColor="gray.600">
            <CardBody textAlign="center" py={12}>
              <Text color="gray.400">  .</Text>
            </CardBody>
          </Card>
        )}
      </VStack>

      <Modal isOpen={isOpen} onClose={onClose} isCentered>
        <ModalOverlay />
        <ModalContent bg="gray.800" color="white" mx={4}>
          <ModalHeader color="#7A5AF8"></ModalHeader>
          <ModalCloseButton />
          <ModalBody pb={6}>
            <VStack spacing={4}>
              <Textarea
                placeholder="  ..."
                value={question}
                onChange={(e) => setQuestion(e.target.value)}
                minH="120px"
                bg="gray.700"
                color="white"
                _placeholder={{ color: "gray.400" }}
                borderColor="gray.600"
                _focus={{ borderColor: "#7A5AF8" }}
              />
              <HStack w="100%" spacing={3}>
                <Button onClick={onClose} variant="outline" borderColor="gray.600" color="white" _hover={{ bg: "gray.700" }} flex={1}>
                  
                </Button>
                <Button 
                  bg="#7A5AF8"
                  color="white"
                  _hover={{ bg: "#6B4CE6" }}
                  onClick={handleSubmitQuestion}
                  flex={1}
                >
                  
                </Button>
              </HStack>
            </VStack>
          </ModalBody>
        </ModalContent>
      </Modal>
    </Box>
  );
};

interface AdminAnswerFormProps {
  questionId: string;
  onSubmit: (answer: string) => void;
}

const AdminAnswerForm: React.FC<AdminAnswerFormProps> = ({ onSubmit }) => {
  const [answer, setAnswer] = useState('');
  const toast = useToast();

  const handleSubmit = () => {
    if (!answer.trim()) {
      toast({
        title: ' ',
        status: 'warning',
        duration: 2000,
      });
      return;
    }

    onSubmit(answer.trim());
    setAnswer('');
    toast({
      title: ' ',
      status: 'success',
      duration: 2000,
    });
  };

  return (
    <VStack spacing={3} align="stretch">
      <Text fontWeight="semibold" color="#7A5AF8"> </Text>
      <Textarea
        placeholder=" ..."
        value={answer}
        onChange={(e) => setAnswer(e.target.value)}
        minH="80px"
        bg="gray.700"
        color="white"
        _placeholder={{ color: "gray.400" }}
        borderColor="gray.600"
        _focus={{ borderColor: "#7A5AF8" }}
      />
      <Button 
        bg="#7A5AF8"
        color="white"
        _hover={{ bg: "#6B4CE6" }}
        size="sm" 
        alignSelf="flex-end"
        onClick={handleSubmit}
      >
         
      </Button>
    </VStack>
  );
};