import React, { useState } from 'react';
import {
  Box,
  VStack,
  HStack,
  Text,
  Button,
  Card,
  CardBody,
  Badge,
  Grid,
  GridItem,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalCloseButton,
  ModalBody,
  useDisclosure,
  useToast,
  Alert,
  AlertIcon,
  Divider,
  List,
  ListItem,
  ListIcon
} from '@chakra-ui/react';
import { CheckCircleIcon } from '@chakra-ui/icons';
import { useAuth } from '../../contexts/AuthContext';

interface PaymentItem {
  id: string;
  title: string;
  description: string;
  price: number;
  school: string;
  grade: number;
  category: 'textbook' | 'workbook' | 'test';
  isRequired: boolean;
}

const mockPaymentItems: PaymentItem[] = [
  {
    id: '1',
    title: ' ',
    description: '      ',
    price: 25000,
    school: '',
    grade: 2,
    category: 'textbook',
    isRequired: true
  },
  {
    id: '2',
    title: '  ',
    description: '     ',
    price: 18000,
    school: '',
    grade: 2,
    category: 'workbook',
    isRequired: true
  },
  {
    id: '3',
    title: '  (3)',
    description: '3  10 + ',
    price: 15000,
    school: '',
    grade: 2,
    category: 'test',
    isRequired: false
  },
  {
    id: '4',
    title: '  ',
    description: '      ',
    price: 22000,
    school: '',
    grade: 3,
    category: 'textbook',
    isRequired: true
  },
  {
    id: '5',
    title: '  5',
    description: '   5  + ',
    price: 28000,
    school: '',
    grade: 3,
    category: 'workbook',
    isRequired: true
  }
];

export const PaymentPage: React.FC = () => {
  const { user } = useAuth();
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [selectedItems, setSelectedItems] = useState<PaymentItem[]>([]);
  const [isProcessing, setIsProcessing] = useState(false);
  const toast = useToast();

  if (!user || user.role !== 'student') {
    return (
      <Box textAlign="center" py={20} bg="gray.900" minH="100vh" color="white">
        <Alert status="warning" maxW="md" mx="auto" bg="orange.800" color="white">
          <AlertIcon />
              .
        </Alert>
      </Box>
    );
  }

  const userProfile = user.profile;
  const availableItems = mockPaymentItems.filter(item => 
    item.school === userProfile?.school && 
    item.grade === userProfile?.grade
  );

  const requiredItems = availableItems.filter(item => item.isRequired);
  const optionalItems = availableItems.filter(item => !item.isRequired);

  const handleSelectItem = (item: PaymentItem) => {
    setSelectedItems(prev => {
      const isSelected = prev.some(selected => selected.id === item.id);
      if (isSelected) {
        return prev.filter(selected => selected.id !== item.id);
      } else {
        return [...prev, item];
      }
    });
  };

  const handlePayment = () => {
    if (selectedItems.length === 0) {
      toast({
        title: '  ',
        status: 'warning',
        duration: 2000,
      });
      return;
    }

    onOpen();
  };

  const processPayment = () => {
    setIsProcessing(true);
    
    //   
    setTimeout(() => {
      setIsProcessing(false);
      onClose();
      setSelectedItems([]);
      
      toast({
        title: ' ',
        description: '   .',
        status: 'success',
        duration: 4000,
      });
    }, 2000);
  };

  const totalAmount = selectedItems.reduce((sum, item) => sum + item.price, 0);

  const getCategoryLabel = (category: string) => {
    switch (category) {
      case 'textbook': return '';
      case 'workbook': return '';
      case 'test': return '';
      default: return '';
    }
  };

  return (
    <Box maxW="1200px" mx="auto" p={6} bg="gray.900" minH="100vh" color="white">
      <VStack spacing={6} align="stretch">
        <Text fontSize="3xl" fontWeight="bold" color="#7A5AF8">
            - {userProfile?.school} {userProfile?.grade}
        </Text>

        <Alert status="info" bg="blue.800" color="white">
          <AlertIcon />
                .     .
        </Alert>

        {/*   */}
        <Box>
          <Text fontSize="xl" fontWeight="bold" color="#7A5AF8" mb={4}>
             
          </Text>
          <Grid templateColumns={{ base: '1fr', md: 'repeat(2, 1fr)' }} gap={4}>
            {requiredItems.map((item) => {
              const isSelected = selectedItems.some(selected => selected.id === item.id);
              return (
                <GridItem key={item.id}>
                  <Card 
                    bg={isSelected ? "purple.900" : "gray.800"} 
                    border="2px solid" 
                    borderColor={isSelected ? "#7A5AF8" : "gray.600"}
                    cursor="pointer"
                    onClick={() => handleSelectItem(item)}
                    _hover={{ borderColor: "#7A5AF8" }}
                  >
                    <CardBody>
                      <VStack spacing={3} align="stretch">
                        <HStack justify="space-between">
                          <Badge colorScheme="red" variant="solid"></Badge>
                          <Badge colorScheme="gray">{getCategoryLabel(item.category)}</Badge>
                        </HStack>
                        
                        <Text fontWeight="bold" color="white">{item.title}</Text>
                        <Text fontSize="sm" color="gray.300">{item.description}</Text>
                        
                        <HStack justify="space-between" mt={4}>
                          <Text fontSize="lg" fontWeight="bold" color="#7A5AF8">
                            {item.price.toLocaleString()}
                          </Text>
                          {isSelected && (
                            <CheckCircleIcon color="#7A5AF8" />
                          )}
                        </HStack>
                      </VStack>
                    </CardBody>
                  </Card>
                </GridItem>
              );
            })}
          </Grid>
        </Box>

        {/*   */}
        {optionalItems.length > 0 && (
          <Box>
            <Text fontSize="xl" fontWeight="bold" color="#7A5AF8" mb={4}>
               
            </Text>
            <Grid templateColumns={{ base: '1fr', md: 'repeat(2, 1fr)' }} gap={4}>
              {optionalItems.map((item) => {
                const isSelected = selectedItems.some(selected => selected.id === item.id);
                return (
                  <GridItem key={item.id}>
                    <Card 
                      bg={isSelected ? "purple.900" : "gray.800"} 
                      border="2px solid" 
                      borderColor={isSelected ? "#7A5AF8" : "gray.600"}
                      cursor="pointer"
                      onClick={() => handleSelectItem(item)}
                      _hover={{ borderColor: "#7A5AF8" }}
                    >
                      <CardBody>
                        <VStack spacing={3} align="stretch">
                          <HStack justify="space-between">
                            <Badge colorScheme="blue" variant="solid"></Badge>
                            <Badge colorScheme="gray">{getCategoryLabel(item.category)}</Badge>
                          </HStack>
                          
                          <Text fontWeight="bold" color="white">{item.title}</Text>
                          <Text fontSize="sm" color="gray.300">{item.description}</Text>
                          
                          <HStack justify="space-between" mt={4}>
                            <Text fontSize="lg" fontWeight="bold" color="#7A5AF8">
                              {item.price.toLocaleString()}
                            </Text>
                            {isSelected && (
                              <CheckCircleIcon color="#7A5AF8" />
                            )}
                          </HStack>
                        </VStack>
                      </CardBody>
                    </Card>
                  </GridItem>
                );
              })}
            </Grid>
          </Box>
        )}

        {availableItems.length === 0 && (
          <Card bg="gray.800" border="1px solid" borderColor="gray.600">
            <CardBody textAlign="center" py={12}>
              <Text color="gray.400">
                    .
              </Text>
            </CardBody>
          </Card>
        )}

        {/*   */}
        {selectedItems.length > 0 && (
          <Card bg="gray.800" border="2px solid" borderColor="#7A5AF8">
            <CardBody>
              <VStack spacing={4} align="stretch">
                <Text fontSize="lg" fontWeight="bold" color="#7A5AF8">
                    ({selectedItems.length})
                </Text>
                <List spacing={2}>
                  {selectedItems.map((item) => (
                    <ListItem key={item.id} color="white">
                      <ListIcon as={CheckCircleIcon} color="#7A5AF8" />
                      {item.title} - {item.price.toLocaleString()}
                    </ListItem>
                  ))}
                </List>
                <Divider borderColor="gray.600" />
                <HStack justify="space-between">
                  <Text fontSize="xl" fontWeight="bold" color="white">
                     
                  </Text>
                  <Text fontSize="xl" fontWeight="bold" color="#7A5AF8">
                    {totalAmount.toLocaleString()}
                  </Text>
                </HStack>
                <Button 
                  bg="#7A5AF8"
                  color="white"
                  _hover={{ bg: "#6B4CE6" }}
                  size="lg"
                  onClick={handlePayment}
                >
                  
                </Button>
              </VStack>
            </CardBody>
          </Card>
        )}
      </VStack>

      {/*    */}
      <Modal isOpen={isOpen} onClose={onClose} isCentered>
        <ModalOverlay />
        <ModalContent bg="gray.800" color="white" mx={4}>
          <ModalHeader color="#7A5AF8"> </ModalHeader>
          <ModalCloseButton />
          <ModalBody pb={6}>
            <VStack spacing={4}>
              <Text textAlign="center">
                 <Text as="span" color="#7A5AF8" fontWeight="bold">{totalAmount.toLocaleString()}</Text> ?
              </Text>
              
              <Box w="100%" bg="gray.700" p={4} borderRadius="md">
                <Text fontSize="sm" fontWeight="bold" mb={2}> :</Text>
                {selectedItems.map((item) => (
                  <Text key={item.id} fontSize="sm" color="gray.300">
                     {item.title} - {item.price.toLocaleString()}
                  </Text>
                ))}
              </Box>

              <Text fontSize="sm" color="gray.400" textAlign="center">
                      .
              </Text>
              
              <HStack w="100%" spacing={3}>
                <Button 
                  onClick={onClose} 
                  variant="outline" 
                  borderColor="gray.600" 
                  color="white" 
                  _hover={{ bg: "gray.700" }} 
                  flex={1}
                  disabled={isProcessing}
                >
                  
                </Button>
                <Button 
                  bg="#7A5AF8"
                  color="white"
                  _hover={{ bg: "#6B4CE6" }}
                  onClick={processPayment}
                  flex={1}
                  isLoading={isProcessing}
                  loadingText=" ..."
                >
                   
                </Button>
              </HStack>
            </VStack>
          </ModalBody>
        </ModalContent>
      </Modal>
    </Box>
  );
};