import React, { useState } from 'react';
import {
  Box,
  VStack,
  HStack,
  Text,
  Button,
  Card,
  CardBody,
  Badge,
  Grid,
  GridItem,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalCloseButton,
  ModalBody,
  useDisclosure,
  useToast,
  Alert,
  AlertIcon,
  Select,
  Textarea,
  Divider
} from '@chakra-ui/react';
import { useAuth } from '../../contexts/AuthContext';

interface ClinicSlot {
  id: string;
  date: string;
  time: string;
  isAvailable: boolean;
  reservedBy?: string;
  description?: string;
}

interface ClinicReservation {
  id: string;
  studentId: string;
  date: string;
  time: string;
  subject: string;
  description: string;
  status: 'pending' | 'confirmed' | 'completed' | 'cancelled';
}

const mockClinicSlots: ClinicSlot[] = [
  { id: '1', date: '2024-03-18', time: '14:00', isAvailable: true },
  { id: '2', date: '2024-03-18', time: '15:00', isAvailable: false, reservedBy: '' },
  { id: '3', date: '2024-03-18', time: '16:00', isAvailable: true },
  { id: '4', date: '2024-03-19', time: '14:00', isAvailable: true },
  { id: '5', date: '2024-03-19', time: '15:00', isAvailable: true },
  { id: '6', date: '2024-03-19', time: '16:00', isAvailable: false, reservedBy: '' },
  { id: '7', date: '2024-03-20', time: '14:00', isAvailable: true },
  { id: '8', date: '2024-03-20', time: '15:00', isAvailable: true },
  { id: '9', date: '2024-03-20', time: '16:00', isAvailable: true },
  { id: '10', date: '2024-03-21', time: '14:00', isAvailable: false, reservedBy: '' },
  { id: '11', date: '2024-03-21', time: '15:00', isAvailable: true },
  { id: '12', date: '2024-03-21', time: '16:00', isAvailable: true },
];

const mockReservations: ClinicReservation[] = [
  {
    id: '1',
    studentId: '1',
    date: '2024-03-15',
    time: '15:00',
    subject: '  ',
    description: '   .',
    status: 'completed'
  }
];

export const ClinicPage: React.FC = () => {
  const { user } = useAuth();
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [selectedSlot, setSelectedSlot] = useState<ClinicSlot | null>(null);
  const [subject, setSubject] = useState('');
  const [description, setDescription] = useState('');
  const [reservations, setReservations] = useState<ClinicReservation[]>(mockReservations);
  const toast = useToast();

  if (!user || user.role !== 'student') {
    return (
      <Box textAlign="center" py={20} bg="gray.900" minH="100vh" color="white">
        <Alert status="warning" maxW="md" mx="auto" bg="orange.800" color="white">
          <AlertIcon />
              .
        </Alert>
      </Box>
    );
  }

  const userReservations = reservations.filter(r => r.studentId === user.id);

  const handleSlotClick = (slot: ClinicSlot) => {
    if (!slot.isAvailable) return;
    setSelectedSlot(slot);
    onOpen();
  };

  const handleReservation = () => {
    if (!selectedSlot || !subject.trim() || !description.trim()) {
      toast({
        title: '  ',
        status: 'warning',
        duration: 2000,
      });
      return;
    }

    const newReservation: ClinicReservation = {
      id: Date.now().toString(),
      studentId: user.id,
      date: selectedSlot.date,
      time: selectedSlot.time,
      subject: subject.trim(),
      description: description.trim(),
      status: 'pending'
    };

    setReservations([...reservations, newReservation]);
    setSubject('');
    setDescription('');
    onClose();
    
    toast({
      title: '  ',
      description: '    .',
      status: 'success',
      duration: 4000,
    });
  };

  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    const days = ['', '', '', '', '', '', ''];
    const month = date.getMonth() + 1;
    const day = date.getDate();
    const dayOfWeek = days[date.getDay()];
    return `${month} ${day} (${dayOfWeek})`;
  };

  const getStatusBadge = (status: string) => {
    switch (status) {
      case 'pending': return <Badge colorScheme="yellow"></Badge>;
      case 'confirmed': return <Badge colorScheme="blue"></Badge>;
      case 'completed': return <Badge colorScheme="green"></Badge>;
      case 'cancelled': return <Badge colorScheme="red"></Badge>;
      default: return <Badge>  </Badge>;
    }
  };

  //  
  const groupedSlots = mockClinicSlots.reduce((groups, slot) => {
    const date = slot.date;
    if (!groups[date]) {
      groups[date] = [];
    }
    groups[date].push(slot);
    return groups;
  }, {} as Record<string, ClinicSlot[]>);

  return (
    <Box maxW="1200px" mx="auto" p={6} bg="gray.900" minH="100vh" color="white">
      <VStack spacing={6} align="stretch">
        <Text fontSize="3xl" fontWeight="bold" color="#7A5AF8">
             
        </Text>

        <Alert status="info" bg="blue.800" color="white">
          <AlertIcon />
          1:1       .    .
        </Alert>

        {/*    */}
        <Box>
          <Text fontSize="xl" fontWeight="bold" color="#7A5AF8" mb={4}>
              
          </Text>
          {userReservations.length > 0 ? (
            <VStack spacing={3} align="stretch">
              {userReservations.map((reservation) => (
                <Card key={reservation.id} bg="gray.800" border="1px solid" borderColor="gray.600">
                  <CardBody>
                    <Grid templateColumns="1fr auto" gap={4} alignItems="center">
                      <VStack align="stretch" spacing={2}>
                        <HStack>
                          <Text fontWeight="bold" color="white">
                            {formatDate(reservation.date)} {reservation.time}
                          </Text>
                          {getStatusBadge(reservation.status)}
                        </HStack>
                        <Text fontSize="sm" color="gray.300">
                          : {reservation.subject}
                        </Text>
                        <Text fontSize="sm" color="gray.400">
                          {reservation.description}
                        </Text>
                      </VStack>
                    </Grid>
                  </CardBody>
                </Card>
              ))}
            </VStack>
          ) : (
            <Card bg="gray.800" border="1px solid" borderColor="gray.600">
              <CardBody textAlign="center" py={8}>
                <Text color="gray.400">  .</Text>
              </CardBody>
            </Card>
          )}
        </Box>

        {/*    */}
        <Box>
          <Text fontSize="xl" fontWeight="bold" color="#7A5AF8" mb={4}>
              
          </Text>
          <VStack spacing={4} align="stretch">
            {Object.entries(groupedSlots).map(([date, slots]) => (
              <Card key={date} bg="gray.800" border="1px solid" borderColor="gray.600">
                <CardBody>
                  <Text fontWeight="bold" color="white" mb={4}>
                    {formatDate(date)}
                  </Text>
                  <Grid templateColumns={{ base: 'repeat(2, 1fr)', md: 'repeat(3, 1fr)' }} gap={3}>
                    {slots.map((slot) => (
                      <Button
                        key={slot.id}
                        onClick={() => handleSlotClick(slot)}
                        bg={slot.isAvailable ? "gray.700" : "gray.600"}
                        color={slot.isAvailable ? "white" : "gray.400"}
                        _hover={slot.isAvailable ? { bg: "#7A5AF8" } : {}}
                        cursor={slot.isAvailable ? "pointer" : "not-allowed"}
                        size="sm"
                        variant="solid"
                        disabled={!slot.isAvailable}
                      >
                        {slot.time}
                        {!slot.isAvailable && (
                          <Text fontSize="xs" ml={1}>()</Text>
                        )}
                      </Button>
                    ))}
                  </Grid>
                </CardBody>
              </Card>
            ))}
          </VStack>
        </Box>
      </VStack>

      {/*    */}
      <Modal isOpen={isOpen} onClose={onClose} size="md" isCentered>
        <ModalOverlay />
        <ModalContent bg="gray.800" color="white" mx={4}>
          <ModalHeader color="#7A5AF8">  </ModalHeader>
          <ModalCloseButton />
          <ModalBody pb={6}>
            <VStack spacing={4}>
              <Box w="100%" bg="gray.700" p={4} borderRadius="md">
                <Text fontWeight="bold" mb={2}> </Text>
                <Text color="#7A5AF8">
                  {selectedSlot && `${formatDate(selectedSlot.date)} ${selectedSlot.time}`}
                </Text>
              </Box>

              <Box w="100%">
                <Text mb={2} fontWeight="semibold">
                    
                </Text>
                <Select 
                  placeholder=" "
                  value={subject}
                  onChange={(e) => setSubject(e.target.value)}
                  bg="gray.700"
                  color="white"
                  borderColor="gray.600"
                  _focus={{ borderColor: "#7A5AF8" }}
                >
                  <option value="  ">  </option>
                  <option value="  ">  </option>
                  <option value="  ">  </option>
                  <option value=" "> </option>
                  <option value="  ">  </option>
                  <option value=""></option>
                </Select>
              </Box>

              <Box w="100%">
                <Text mb={2} fontWeight="semibold">
                   
                </Text>
                <Textarea
                  placeholder="      ."
                  value={description}
                  onChange={(e) => setDescription(e.target.value)}
                  minH="100px"
                  bg="gray.700"
                  color="white"
                  _placeholder={{ color: "gray.400" }}
                  borderColor="gray.600"
                  _focus={{ borderColor: "#7A5AF8" }}
                />
              </Box>
              
              <HStack w="100%" spacing={3}>
                <Button 
                  onClick={onClose} 
                  variant="outline" 
                  borderColor="gray.600" 
                  color="white" 
                  _hover={{ bg: "gray.700" }} 
                  flex={1}
                >
                  
                </Button>
                <Button 
                  bg="#7A5AF8"
                  color="white"
                  _hover={{ bg: "#6B4CE6" }}
                  onClick={handleReservation}
                  flex={1}
                >
                   
                </Button>
              </HStack>
            </VStack>
          </ModalBody>
        </ModalContent>
      </Modal>
    </Box>
  );
};