import React, { useState, useEffect } from 'react';
import {
  Box,
  VStack,
  HStack,
  Text,
  Button,
  Input,
  Select,
  Card,
  CardBody,
  Badge,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalCloseButton,
  ModalBody,
  useDisclosure,
  useToast,
  Grid,
  GridItem,
  IconButton,
  AlertDialog,
  AlertDialogOverlay,
  AlertDialogContent,
  AlertDialogHeader,
  AlertDialogBody,
  AlertDialogFooter,
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  FormControl,
  FormLabel
} from '@chakra-ui/react';
import { AddIcon, EditIcon, DeleteIcon } from '@chakra-ui/icons';
import { User, StudentProfile } from '../../data/users';

interface AdminStudentManagerProps {
  onUpdate: () => void;
}

interface StudentData extends User {
  profile: StudentProfile;
}

export const AdminStudentManager: React.FC<AdminStudentManagerProps> = ({ onUpdate }) => {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const { isOpen: isDeleteOpen, onOpen: onDeleteOpen, onClose: onDeleteClose } = useDisclosure();
  const [students, setStudents] = useState<StudentData[]>([]);
  const [editingStudent, setEditingStudent] = useState<StudentData | null>(null);
  const [deleteStudentId, setDeleteStudentId] = useState<string>('');
  const [formData, setFormData] = useState({
    username: '',
    password: '',
    name: '',
    school: '',
    grade: 2,
    academy: '',
    schedule: ''
  });
  const toast = useToast();
  const cancelRef = React.useRef<HTMLButtonElement>(null);

  const schools = ['', '', '', '', ''];
  const grades = [1, 2, 3];
  const schedules = [
    ' 18:00-20:00',
    ' 18:00-20:00',
    ' 18:00-20:00',
    ' 18:00-20:00',
    ' 18:00-20:00',
    ' 14:00-16:00',
    ' 16:00-18:00',
    ' 14:00-16:00',
    ' 16:00-18:00'
  ];

  useEffect(() => {
    loadStudents();
  }, []);

  const loadStudents = () => {
    const storedUsers = JSON.parse(localStorage.getItem('dongin_users') || '[]');
    const studentUsers = storedUsers.filter((user: User) => user.role === 'student');
    setStudents(studentUsers);
  };

  const saveStudents = (updatedStudents: StudentData[]) => {
    const storedUsers = JSON.parse(localStorage.getItem('dongin_users') || '[]');
    const adminUsers = storedUsers.filter((user: User) => user.role === 'admin');
    const allUsers = [...adminUsers, ...updatedStudents];
    localStorage.setItem('dongin_users', JSON.stringify(allUsers));
  };

  const handleSubmit = () => {
    if (!formData.username.trim() || !formData.password.trim() || !formData.name.trim() || !formData.school || !formData.schedule) {
      toast({
        title: '  ',
        status: 'warning',
        duration: 2000,
      });
      return;
    }

    //   
    const existingUser = students.find(student => 
      student.username === formData.username.trim() && student.id !== editingStudent?.id
    );
    
    if (existingUser) {
      toast({
        title: '  ',
        status: 'error',
        duration: 2000,
      });
      return;
    }

    const newStudent: StudentData = {
      id: editingStudent?.id || Date.now().toString(),
      username: formData.username.trim(),
      password: formData.password,
      role: 'student',
      profile: {
        name: formData.name.trim(),
        school: formData.school,
        grade: formData.grade,
        academy: formData.academy,
        schedule: formData.schedule
      }
    };

    let updatedStudents;
    if (editingStudent) {
      updatedStudents = students.map(student => 
        student.id === editingStudent.id ? newStudent : student
      );
    } else {
      updatedStudents = [newStudent, ...students];
    }

    setStudents(updatedStudents);
    saveStudents(updatedStudents);
    
    resetForm();
    onClose();
    onUpdate();
    
    toast({
      title: editingStudent ? '  ' : ' ',
      status: 'success',
      duration: 2000,
    });
  };

  const handleEdit = (student: StudentData) => {
    setEditingStudent(student);
    setFormData({
      username: student.username,
      password: student.password,
      name: student.profile.name,
      school: student.profile.school,
      grade: student.profile.grade,
      academy: student.profile.academy,
      schedule: student.profile.schedule
    });
    onOpen();
  };

  const handleDelete = (studentId: string) => {
    setDeleteStudentId(studentId);
    onDeleteOpen();
  };

  const confirmDelete = () => {
    const updatedStudents = students.filter(student => student.id !== deleteStudentId);
    setStudents(updatedStudents);
    saveStudents(updatedStudents);
    
    onDeleteClose();
    onUpdate();
    
    toast({
      title: ' ',
      status: 'success',
      duration: 2000,
    });
  };

  const resetForm = () => {
    setFormData({
      username: '',
      password: '',
      name: '',
      school: '',
      grade: 2,
      academy: '',
      schedule: ''
    });
    setEditingStudent(null);
  };

  return (
    <Box>
      <HStack justify="space-between" mb={6}>
        <Text fontSize="2xl" fontWeight="bold" color="white">
            ({students.length})
        </Text>
        <Button
          leftIcon={<AddIcon />}
          bg="#7A5AF8"
          color="white"
          _hover={{ bg: "#6B4CE6" }}
          onClick={() => {
            resetForm();
            onOpen();
          }}
        >
            
        </Button>
      </HStack>

      {/*   */}
      <Grid templateColumns={{ base: '1fr', md: 'repeat(3, 1fr)' }} gap={4} mb={6}>
        <GridItem>
          <Card bg="gray.800" border="1px solid" borderColor="gray.600">
            <CardBody textAlign="center">
              <Text color="gray.300" fontSize="sm">  </Text>
              <Text color="#7A5AF8" fontSize="2xl" fontWeight="bold">
                {students.length}
              </Text>
            </CardBody>
          </Card>
        </GridItem>
        
        <GridItem>
          <Card bg="gray.800" border="1px solid" borderColor="gray.600">
            <CardBody textAlign="center">
              <Text color="gray.300" fontSize="sm">3 </Text>
              <Text color="red.400" fontSize="2xl" fontWeight="bold">
                {students.filter(s => s.profile.grade === 3).length}
              </Text>
            </CardBody>
          </Card>
        </GridItem>
        
        <GridItem>
          <Card bg="gray.800" border="1px solid" borderColor="gray.600">
            <CardBody textAlign="center">
              <Text color="gray.300" fontSize="sm">2 </Text>
              <Text color="blue.400" fontSize="2xl" fontWeight="bold">
                {students.filter(s => s.profile.grade === 2).length}
              </Text>
            </CardBody>
          </Card>
        </GridItem>
      </Grid>

      {/*   */}
      <Card bg="gray.800" border="1px solid" borderColor="gray.600">
        <CardBody>
          {students.length > 0 ? (
            <Box overflowX="auto">
              <Table variant="simple" size="sm">
                <Thead>
                  <Tr>
                    <Th color="gray.300" borderColor="gray.600"></Th>
                    <Th color="gray.300" borderColor="gray.600"></Th>
                    <Th color="gray.300" borderColor="gray.600"></Th>
                    <Th color="gray.300" borderColor="gray.600"></Th>
                    <Th color="gray.300" borderColor="gray.600"> </Th>
                    <Th color="gray.300" borderColor="gray.600"></Th>
                  </Tr>
                </Thead>
                <Tbody>
                  {students.map((student) => (
                    <Tr key={student.id}>
                      <Td color="white" borderColor="gray.600" fontWeight="bold">
                        {student.username}
                      </Td>
                      <Td color="white" borderColor="gray.600">
                        <Badge colorScheme="blue" size="sm">
                          {student.profile.name}
                        </Badge>
                      </Td>
                      <Td color="gray.300" borderColor="gray.600" fontSize="sm">
                        {student.profile.school}
                      </Td>
                      <Td borderColor="gray.600">
                        <Badge colorScheme={
                          student.profile.grade === 3 ? 'red' :
                          student.profile.grade === 2 ? 'blue' : 'green'
                        } size="sm">
                          {student.profile.grade}
                        </Badge>
                      </Td>
                      <Td color="gray.300" borderColor="gray.600" fontSize="sm">
                        {student.profile.schedule}
                      </Td>
                      <Td borderColor="gray.600">
                        <HStack spacing={1}>
                          <IconButton
                            aria-label=""
                            icon={<EditIcon />}
                            size="xs"
                            variant="outline"
                            borderColor="gray.600"
                            color="white"
                            _hover={{ bg: "gray.600" }}
                            onClick={() => handleEdit(student)}
                          />
                          <IconButton
                            aria-label=""
                            icon={<DeleteIcon />}
                            size="xs"
                            variant="outline"
                            borderColor="red.600"
                            color="red.400"
                            _hover={{ bg: "red.900" }}
                            onClick={() => handleDelete(student.id)}
                          />
                        </HStack>
                      </Td>
                    </Tr>
                  ))}
                </Tbody>
              </Table>
            </Box>
          ) : (
            <Text color="gray.400" textAlign="center" py={8}>
                .
            </Text>
          )}
        </CardBody>
      </Card>

      {/*  /  */}
      <Modal isOpen={isOpen} onClose={onClose} size="lg" isCentered>
        <ModalOverlay />
        <ModalContent bg="gray.800" color="white" mx={4}>
          <ModalHeader color="#7A5AF8">
            {editingStudent ? '  ' : '  '}
          </ModalHeader>
          <ModalCloseButton />
          <ModalBody pb={6}>
            <VStack spacing={4}>
              <Grid templateColumns={{ base: '1fr', md: 'repeat(2, 1fr)' }} gap={4} w="100%">
                <GridItem>
                  <FormControl isRequired>
                    <FormLabel></FormLabel>
                    <Input
                      placeholder=" "
                      value={formData.username}
                      onChange={(e) => setFormData({ ...formData, username: e.target.value })}
                      bg="gray.700"
                      borderColor="gray.600"
                      _focus={{ borderColor: "#7A5AF8" }}
                    />
                  </FormControl>
                </GridItem>

                <GridItem>
                  <FormControl isRequired>
                    <FormLabel></FormLabel>
                    <Input
                      type="password"
                      placeholder=""
                      value={formData.password}
                      onChange={(e) => setFormData({ ...formData, password: e.target.value })}
                      bg="gray.700"
                      borderColor="gray.600"
                      _focus={{ borderColor: "#7A5AF8" }}
                    />
                  </FormControl>
                </GridItem>

                <GridItem>
                  <FormControl isRequired>
                    <FormLabel></FormLabel>
                    <Input
                      placeholder=" "
                      value={formData.name}
                      onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                      bg="gray.700"
                      borderColor="gray.600"
                      _focus={{ borderColor: "#7A5AF8" }}
                    />
                  </FormControl>
                </GridItem>

                <GridItem>
                  <FormControl isRequired>
                    <FormLabel></FormLabel>
                    <Select
                      value={formData.grade}
                      onChange={(e) => setFormData({ ...formData, grade: Number(e.target.value) })}
                      bg="gray.700"
                      borderColor="gray.600"
                      _focus={{ borderColor: "#7A5AF8" }}
                    >
                      {grades.map(grade => (
                        <option key={grade} value={grade}>{grade}</option>
                      ))}
                    </Select>
                  </FormControl>
                </GridItem>

                <GridItem colSpan={{ base: 1, md: 2 }}>
                  <FormControl isRequired>
                    <FormLabel></FormLabel>
                    <Select
                      placeholder=" "
                      value={formData.school}
                      onChange={(e) => setFormData({ ...formData, school: e.target.value })}
                      bg="gray.700"
                      borderColor="gray.600"
                      _focus={{ borderColor: "#7A5AF8" }}
                    >
                      {schools.map(school => (
                        <option key={school} value={school}>{school}</option>
                      ))}
                    </Select>
                  </FormControl>
                </GridItem>

                <GridItem>
                  <FormControl>
                    <FormLabel> </FormLabel>
                    <Input
                      value={formData.academy}
                      onChange={(e) => setFormData({ ...formData, academy: e.target.value })}
                      bg="gray.700"
                      borderColor="gray.600"
                      _focus={{ borderColor: "#7A5AF8" }}
                    />
                  </FormControl>
                </GridItem>

                <GridItem>
                  <FormControl isRequired>
                    <FormLabel> </FormLabel>
                    <Select
                      placeholder="  "
                      value={formData.schedule}
                      onChange={(e) => setFormData({ ...formData, schedule: e.target.value })}
                      bg="gray.700"
                      borderColor="gray.600"
                      _focus={{ borderColor: "#7A5AF8" }}
                    >
                      {schedules.map(schedule => (
                        <option key={schedule} value={schedule}>{schedule}</option>
                      ))}
                    </Select>
                  </FormControl>
                </GridItem>
              </Grid>

              <HStack w="100%" spacing={3}>
                <Button 
                  onClick={() => {
                    resetForm();
                    onClose();
                  }} 
                  variant="outline" 
                  borderColor="gray.600" 
                  color="white" 
                  _hover={{ bg: "gray.700" }} 
                  flex={1}
                >
                  
                </Button>
                <Button 
                  bg="#7A5AF8"
                  color="white"
                  _hover={{ bg: "#6B4CE6" }}
                  onClick={handleSubmit}
                  flex={1}
                >
                  {editingStudent ? '' : ''}
                </Button>
              </HStack>
            </VStack>
          </ModalBody>
        </ModalContent>
      </Modal>

      {/*    */}
      <AlertDialog
        isOpen={isDeleteOpen}
        leastDestructiveRef={cancelRef}
        onClose={onDeleteClose}
        isCentered
      >
        <AlertDialogOverlay>
          <AlertDialogContent bg="gray.800" color="white" mx={4}>
            <AlertDialogHeader fontSize="lg" fontWeight="bold" color="#7A5AF8">
               
            </AlertDialogHeader>
            <AlertDialogBody>
                 ?     .
            </AlertDialogBody>
            <AlertDialogFooter>
              <Button ref={cancelRef} onClick={onDeleteClose} variant="outline" borderColor="gray.600" color="white">
                
              </Button>
              <Button colorScheme="red" onClick={confirmDelete} ml={3}>
                
              </Button>
            </AlertDialogFooter>
          </AlertDialogContent>
        </AlertDialogOverlay>
      </AlertDialog>
    </Box>
  );
};