import React, { useState, useEffect } from 'react';
import {
  Box,
  VStack,
  HStack,
  Text,
  Card,
  CardBody,
  Badge,
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  Button,
  Select,
  useToast,
  Grid,
  GridItem,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalCloseButton,
  ModalBody,
  useDisclosure,
  Input,
  Textarea,
  IconButton,
  Alert,
  AlertIcon
} from '@chakra-ui/react';
import { ViewIcon, CheckIcon, CloseIcon } from '@chakra-ui/icons';
import { SessionStorageManager, SessionStorageKeys } from '../../utils/sessionStorage';

interface PaymentItem {
  id: string;
  studentId: string;
  studentName: string;
  school: string;
  grade: number;
  itemName: string;
  itemType: 'textbook' | 'workbook' | 'supplement';
  price: number;
  quantity: number;
  totalAmount: number;
  status: 'pending' | 'paid' | 'cancelled' | 'refunded';
  requestDate: string;
  paidDate?: string;
  paymentMethod?: string;
  memo?: string;
}

interface AdminPaymentManagerProps {
  onUpdate: () => void;
}

const mockPayments: PaymentItem[] = [
  {
    id: '1',
    studentId: '1',
    studentName: '',
    school: '',
    grade: 2,
    itemName: '   ',
    itemType: 'textbook',
    price: 25000,
    quantity: 1,
    totalAmount: 25000,
    status: 'pending',
    requestDate: '2024-03-15'
  },
  {
    id: '2',
    studentId: '2',
    studentName: '',
    school: '',
    grade: 2,
    itemName: ' ',
    itemType: 'workbook',
    price: 15000,
    quantity: 2,
    totalAmount: 30000,
    status: 'paid',
    requestDate: '2024-03-10',
    paidDate: '2024-03-12',
    paymentMethod: ''
  },
  {
    id: '3',
    studentId: '3',
    studentName: '',
    school: '',
    grade: 3,
    itemName: '  ',
    itemType: 'supplement',
    price: 20000,
    quantity: 1,
    totalAmount: 20000,
    status: 'pending',
    requestDate: '2024-03-16'
  }
];

export const AdminPaymentManager: React.FC<AdminPaymentManagerProps> = ({ onUpdate }) => {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [payments, setPayments] = useState<PaymentItem[]>(mockPayments);
  const [selectedPayment, setSelectedPayment] = useState<PaymentItem | null>(null);
  const [statusFilter, setStatusFilter] = useState<string>('all');
  const [paymentMethod, setPaymentMethod] = useState('');
  const [memo, setMemo] = useState('');
  const toast = useToast();

  useEffect(() => {
    loadPayments();
  }, []);

  const loadPayments = () => {
    const storedPayments = SessionStorageManager.get<PaymentItem[]>(SessionStorageKeys.PAYMENTS, mockPayments);
    setPayments(storedPayments);
  };

  const handleViewPayment = (payment: PaymentItem) => {
    setSelectedPayment(payment);
    setPaymentMethod(payment.paymentMethod || '');
    setMemo(payment.memo || '');
    onOpen();
  };

  const handleUpdatePaymentStatus = (paymentId: string, newStatus: 'paid' | 'cancelled' | 'refunded') => {
    const updatedPayments = payments.map(payment => {
      if (payment.id === paymentId) {
        const updatedPayment = {
          ...payment,
          status: newStatus,
          paidDate: newStatus === 'paid' ? new Date().toISOString().split('T')[0] : payment.paidDate,
          paymentMethod: newStatus === 'paid' ? paymentMethod : payment.paymentMethod,
          memo: memo || payment.memo
        };
        return updatedPayment;
      }
      return payment;
    });

    setPayments(updatedPayments);
    SessionStorageManager.set(SessionStorageKeys.PAYMENTS, updatedPayments);
    onUpdate();

    const statusText = {
      paid: '',
      cancelled: '',
      refunded: ''
    };

    toast({
      title: `  '${statusText[newStatus]}' `,
      status: 'success',
      duration: 2000,
    });

    onClose();
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'pending':
        return 'orange';
      case 'paid':
        return 'green';
      case 'cancelled':
        return 'red';
      case 'refunded':
        return 'blue';
      default:
        return 'gray';
    }
  };

  const getStatusText = (status: string) => {
    switch (status) {
      case 'pending':
        return '';
      case 'paid':
        return '';
      case 'cancelled':
        return '';
      case 'refunded':
        return '';
      default:
        return status;
    }
  };

  const getItemTypeText = (type: string) => {
    switch (type) {
      case 'textbook':
        return '';
      case 'workbook':
        return '';
      case 'supplement':
        return '';
      default:
        return type;
    }
  };

  const filteredPayments = payments.filter(payment => 
    statusFilter === 'all' || payment.status === statusFilter
  );

  const totalAmount = filteredPayments.reduce((sum, payment) => sum + payment.totalAmount, 0);
  const pendingCount = payments.filter(p => p.status === 'pending').length;
  const paidCount = payments.filter(p => p.status === 'paid').length;

  return (
    <Box>
      <HStack justify="space-between" mb={6}>
        <Text fontSize="2xl" fontWeight="bold" color="white">
             ({payments.length})
        </Text>
        <HStack spacing={4}>
          <Badge colorScheme="orange" fontSize="sm" p={2}>
            : {pendingCount}
          </Badge>
          <Badge colorScheme="green" fontSize="sm" p={2}>
            : {paidCount}
          </Badge>
        </HStack>
      </HStack>

      {/*    */}
      <Grid templateColumns={{ base: '1fr', md: 'repeat(2, 1fr)', lg: 'repeat(4, 1fr)' }} gap={4} mb={6}>
        <GridItem>
          <Card bg="gray.800" border="1px solid" borderColor="gray.600">
            <CardBody textAlign="center">
              <Text color="gray.300" fontSize="sm">  </Text>
              <Text color="#7A5AF8" fontSize="2xl" fontWeight="bold">
                {payments.length}
              </Text>
            </CardBody>
          </Card>
        </GridItem>
        
        <GridItem>
          <Card bg="gray.800" border="1px solid" borderColor="gray.600">
            <CardBody textAlign="center">
              <Text color="gray.300" fontSize="sm"> </Text>
              <Text color="orange.400" fontSize="2xl" fontWeight="bold">
                {pendingCount}
              </Text>
            </CardBody>
          </Card>
        </GridItem>
        
        <GridItem>
          <Card bg="gray.800" border="1px solid" borderColor="gray.600">
            <CardBody textAlign="center">
              <Text color="gray.300" fontSize="sm"> </Text>
              <Text color="green.400" fontSize="2xl" fontWeight="bold">
                {paidCount}
              </Text>
            </CardBody>
          </Card>
        </GridItem>
        
        <GridItem>
          <Card bg="gray.800" border="1px solid" borderColor="gray.600">
            <CardBody textAlign="center">
              <Text color="gray.300" fontSize="sm">  </Text>
              <Text color="yellow.400" fontSize="2xl" fontWeight="bold">
                {totalAmount.toLocaleString()}
              </Text>
            </CardBody>
          </Card>
        </GridItem>
      </Grid>

      {/*  */}
      <Card bg="gray.800" border="1px solid" borderColor="gray.600" mb={6}>
        <CardBody>
          <HStack>
            <Text fontSize="sm" color="gray.300"> :</Text>
            <Select
              value={statusFilter}
              onChange={(e) => setStatusFilter(e.target.value)}
              bg="gray.700"
              borderColor="gray.600"
              color="white"
              size="sm"
              w="150px"
            >
              <option value="all"></option>
              <option value="pending"></option>
              <option value="paid"></option>
              <option value="cancelled"></option>
              <option value="refunded"></option>
            </Select>
          </HStack>
        </CardBody>
      </Card>

      {/*   */}
      <Card bg="gray.800" border="1px solid" borderColor="gray.600">
        <CardBody>
          {filteredPayments.length > 0 ? (
            <Box overflowX="auto">
              <Table variant="simple" size="sm">
                <Thead>
                  <Tr>
                    <Th color="gray.300" borderColor="gray.600"></Th>
                    <Th color="gray.300" borderColor="gray.600">/</Th>
                    <Th color="gray.300" borderColor="gray.600"></Th>
                    <Th color="gray.300" borderColor="gray.600"></Th>
                    <Th color="gray.300" borderColor="gray.600"></Th>
                    <Th color="gray.300" borderColor="gray.600"></Th>
                    <Th color="gray.300" borderColor="gray.600"></Th>
                    <Th color="gray.300" borderColor="gray.600"></Th>
                    <Th color="gray.300" borderColor="gray.600"></Th>
                  </Tr>
                </Thead>
                <Tbody>
                  {filteredPayments.map((payment) => (
                    <Tr key={payment.id}>
                      <Td color="white" borderColor="gray.600">
                        <Badge colorScheme="blue" size="sm">
                          {payment.studentName}
                        </Badge>
                      </Td>
                      <Td color="gray.300" borderColor="gray.600" fontSize="sm">
                        {payment.school}<br/>{payment.grade}
                      </Td>
                      <Td color="white" borderColor="gray.600" fontSize="sm">
                        {payment.itemName}
                      </Td>
                      <Td color="gray.300" borderColor="gray.600" fontSize="sm">
                        {getItemTypeText(payment.itemType)}
                      </Td>
                      <Td color="white" borderColor="gray.600" fontSize="sm">
                        {payment.quantity}
                      </Td>
                      <Td color="white" borderColor="gray.600" fontSize="sm" fontWeight="bold">
                        {payment.totalAmount.toLocaleString()}
                      </Td>
                      <Td borderColor="gray.600">
                        <Badge colorScheme={getStatusColor(payment.status)} size="sm">
                          {getStatusText(payment.status)}
                        </Badge>
                      </Td>
                      <Td color="gray.300" borderColor="gray.600" fontSize="sm">
                        {payment.requestDate}
                      </Td>
                      <Td borderColor="gray.600">
                        <HStack spacing={1}>
                          <IconButton
                            aria-label=""
                            icon={<ViewIcon />}
                            size="xs"
                            variant="outline"
                            borderColor="gray.600"
                            color="white"
                            _hover={{ bg: "gray.600" }}
                            onClick={() => handleViewPayment(payment)}
                          />
                          {payment.status === 'pending' && (
                            <>
                              <IconButton
                                aria-label=""
                                icon={<CheckIcon />}
                                size="xs"
                                variant="outline"
                                borderColor="green.600"
                                color="green.400"
                                _hover={{ bg: "green.900" }}
                                onClick={() => {
                                  setSelectedPayment(payment);
                                  handleUpdatePaymentStatus(payment.id, 'paid');
                                }}
                              />
                              <IconButton
                                aria-label=""
                                icon={<CloseIcon />}
                                size="xs"
                                variant="outline"
                                borderColor="red.600"
                                color="red.400"
                                _hover={{ bg: "red.900" }}
                                onClick={() => handleUpdatePaymentStatus(payment.id, 'cancelled')}
                              />
                            </>
                          )}
                        </HStack>
                      </Td>
                    </Tr>
                  ))}
                </Tbody>
              </Table>
            </Box>
          ) : (
            <Alert status="info" bg="blue.800" color="white">
              <AlertIcon />
                   .
            </Alert>
          )}
        </CardBody>
      </Card>

      {/*     */}
      <Modal isOpen={isOpen} onClose={onClose} size="lg" isCentered>
        <ModalOverlay />
        <ModalContent bg="gray.800" color="white" mx={4}>
          <ModalHeader color="#7A5AF8">
              
          </ModalHeader>
          <ModalCloseButton />
          <ModalBody pb={6}>
            {selectedPayment && (
              <VStack spacing={4} align="stretch">
                <Grid templateColumns="repeat(2, 1fr)" gap={4}>
                  <Box>
                    <Text fontSize="sm" color="gray.300" mb={1}></Text>
                    <Text fontWeight="bold">{selectedPayment.studentName}</Text>
                  </Box>
                  <Box>
                    <Text fontSize="sm" color="gray.300" mb={1}>/</Text>
                    <Text>{selectedPayment.school} {selectedPayment.grade}</Text>
                  </Box>
                </Grid>

                <Box>
                  <Text fontSize="sm" color="gray.300" mb={1}></Text>
                  <Text fontWeight="bold">{selectedPayment.itemName}</Text>
                </Box>

                <Grid templateColumns="repeat(3, 1fr)" gap={4}>
                  <Box>
                    <Text fontSize="sm" color="gray.300" mb={1}></Text>
                    <Badge colorScheme="purple">
                      {getItemTypeText(selectedPayment.itemType)}
                    </Badge>
                  </Box>
                  <Box>
                    <Text fontSize="sm" color="gray.300" mb={1}></Text>
                    <Text>{selectedPayment.quantity}</Text>
                  </Box>
                  <Box>
                    <Text fontSize="sm" color="gray.300" mb={1}> </Text>
                    <Text fontWeight="bold" color="yellow.400">
                      {selectedPayment.totalAmount.toLocaleString()}
                    </Text>
                  </Box>
                </Grid>

                <Grid templateColumns="repeat(2, 1fr)" gap={4}>
                  <Box>
                    <Text fontSize="sm" color="gray.300" mb={1}></Text>
                    <Badge colorScheme={getStatusColor(selectedPayment.status)}>
                      {getStatusText(selectedPayment.status)}
                    </Badge>
                  </Box>
                  <Box>
                    <Text fontSize="sm" color="gray.300" mb={1}></Text>
                    <Text>{selectedPayment.requestDate}</Text>
                  </Box>
                </Grid>

                {selectedPayment.status === 'paid' && selectedPayment.paidDate && (
                  <Box>
                    <Text fontSize="sm" color="gray.300" mb={1}></Text>
                    <Text color="green.400">{selectedPayment.paidDate}</Text>
                  </Box>
                )}

                {selectedPayment.status === 'pending' && (
                  <>
                    <Box>
                      <Text fontSize="sm" color="gray.300" mb={2}> </Text>
                      <Select
                        placeholder="  "
                        value={paymentMethod}
                        onChange={(e) => setPaymentMethod(e.target.value)}
                        bg="gray.700"
                        borderColor="gray.600"
                        _focus={{ borderColor: "#7A5AF8" }}
                      >
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                      </Select>
                    </Box>

                    <Box>
                      <Text fontSize="sm" color="gray.300" mb={2}></Text>
                      <Textarea
                        placeholder="   ..."
                        value={memo}
                        onChange={(e) => setMemo(e.target.value)}
                        bg="gray.700"
                        borderColor="gray.600"
                        _focus={{ borderColor: "#7A5AF8" }}
                        size="sm"
                        rows={3}
                      />
                    </Box>

                    <HStack spacing={2}>
                      <Button
                        bg="green.600"
                        color="white"
                        _hover={{ bg: "green.500" }}
                        onClick={() => handleUpdatePaymentStatus(selectedPayment.id, 'paid')}
                        flex={1}
                      >
                         
                      </Button>
                      <Button
                        bg="red.600"
                        color="white"
                        _hover={{ bg: "red.500" }}
                        onClick={() => handleUpdatePaymentStatus(selectedPayment.id, 'cancelled')}
                        flex={1}
                      >
                         
                      </Button>
                    </HStack>
                  </>
                )}

                {selectedPayment.memo && (
                  <Box bg="gray.700" p={3} borderRadius="md">
                    <Text fontSize="sm" color="gray.300" mb={1}></Text>
                    <Text fontSize="sm">{selectedPayment.memo}</Text>
                  </Box>
                )}

                <Button
                  onClick={onClose}
                  variant="outline"
                  borderColor="gray.600"
                  color="white"
                  _hover={{ bg: "gray.700" }}
                >
                  
                </Button>
              </VStack>
            )}
          </ModalBody>
        </ModalContent>
      </Modal>
    </Box>
  );
};