export interface Notice {
  id: string;
  title: string;
  content: string;
  author: string;
  createdAt: string;
  category: 'general' | 'school';
  school?: string;
  grade?: number;
}

export interface QnA {
  id: string;
  question: string;
  answer?: string;
  author: string;
  createdAt: string;
  isPrivate: boolean;
}

export interface TestResult {
  id: string;
  studentId: string;
  testName: string;
  score: number;
  rank: number;
  totalStudents: number;
  season: string;
  date: string;
  comment?: string;
}

export interface Homework {
  id: string;
  studentId: string;
  week: number;
  totalProblems: number;
  solvedProblems: number;
  season: string;
  comment?: string;
}

export interface Video {
  id: string;
  title: string;
  description: string;
  school: string;
  grade: number;
  isPublic: boolean;
  validUntil?: string;
  videoUrl: string;
  week: number;
}

